body {
    background-image: url("../assets/wrinkledPaper.jpg");
    background-size: cover;
    height: 98vh;
    width: 100vw;
    margin-left: auto;
    margin-right: auto;
}

* {
    font-family: 'Alkatra', cursive;
}

header, footer {
    height: 5%;
    width: 100%;
    /* border: 1px black solid; */
    font-family: 'Alkatra', cursive;
    font-size: 28px;
}

#linked-in {
    margin-right: 5px;
}


.game-assets {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    width: 100%;
    /* margin-left: auto; */
    /* margin-right: auto; */
    margin-top: 2%;
}

.game-text {
    /* background-color: rgba(255, 255, 255, 0.80); */
}

#score, #lives {
    width: 20%;
}

#score {
    margin-left: 2%;
}

#lives {
    padding-left: 2%;
}

#message {
    width: 60%;
    text-align: center;
}

#sentence {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    /* margin-top: 20px; */
    margin-top: 1%;
}

.game-panels {
    display: flex;
    justify-content: center;
    width: 100%;
    /* height: 550px; */
    margin-left: auto;
    margin-right: auto;
    /* border: none; */
}

#game-panel {
    margin-left: 1%;
    margin-right: 1%;
}

.side-panel {
    padding: 20px;
    /* background-color: rgba(255, 255, 255, 0.80); */
    box-sizing: border-box;
    width: 20%;
    /* border: 1px black solid; */
}

#inner-contents {
    /* background-color: beige; */
    background-image: url("../assets/letterBannerVector.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 85%;
}

#canvas-div {
    overflow: hidden;
    height: 500px;
    /* radi */
}

#game-data {
    margin-top: 1%;
    display: flex;
    justify-content: space-between;
}

.contains-words, #canvas-div {
    border-radius: 7px;
    border: 1px black solid;
    background-color: rgba(255, 255, 255, 0.90);
}

#word-image-container {
    background-image: url("../assets/letterBannerVector.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}

#ux-container {
    height: 95%;
    width: 95%;
}

header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding-top: 1%;
    padding-bottom: 1%;
}

header div {
    /* border: 1px black solid; */
    width: 125px;
}

header img {
    height: 100%;
    width: 100%;
}

#title {
    display: flex;
    justify-content: center;
    align-items: center;
}

#logos {
    display: flex;
    justify-content: space-around;
}

footer {
    margin-bottom: 10%;
}

#footer-spacer {
    height: 5%
}